import * as THREE from 'three';

var idIndex = 0;

class GraphNode {
    constructor(position) {
      const zeroCenterRandom = (scaler) => Math.floor((.5 - Math.random()) * scaler)
      const x = zeroCenterRandom(300);
      const y = zeroCenterRandom(300);
      const z = zeroCenterRandom(300);
      if(idIndex === 0){
        this.pos = new THREE.Vector3( 0,0,0 ); 
        this.pinned = true
      } else {
        this.pos = new THREE.Vector3( x, y, z ); 
        this.pinned = false;        
      }

      this.enabled = true;

      this.id = idIndex;      
      idIndex += 1;      
      this.edges = [];
    }
    connect(other) {
      this.edges.push(other);
      other.edges.push(this);
    }
    hasEdge(other) {
      return this.edges.includes(other);
    }
  }
  
  function everyNode(graph, callback ) {
    for (let node of graph ) {
      callback(node);
    }
  }

  const springLength =  200;
  const springStrength = .3;
  const repulsionStrength = 80000;
  
  function forceDirected(graph, {ignore}) {
    for (let i = 0; i < graph.length; i++ ) {
      let node = graph[i];
      if(!node.enabled) continue;
      for( let j = i + 1; j < graph.length; j++ ) {
        let other = graph[j];
        if(!other.enabled) continue;
        let apart = new THREE.Vector3().copy(other.pos);
        apart.sub(node.pos);
        var distance = other.pos.distanceTo(node.pos);
        distance = Math.max(1, distance);
        let forceSize = -repulsionStrength / (distance * distance);
        if( node.hasEdge(other) ) {
          forceSize += (distance - springLength) * springStrength;
        }
        apart.normalize();
        apart.multiplyScalar(forceSize/2);        
        if(node !== ignore && !node.pinned) node.pos.add(apart);
        if(other !== ignore && !other.pinned) other.pos.sub(apart);      
      }
    }
  }
  
  function everyEdge(graph, callback) {
    for (let i = 0; i < graph.length; i++ ) {
      let node = graph[i];
      for( let j = i + 1; j < graph.length; j++ ) {
        let other = graph[j];
        if( node.hasEdge(other) ) {
          callback(node, other);
        }
      }
    }
  }

  export { GraphNode, forceDirected, everyEdge, everyNode }